@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.posts {
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding-top: 150px;
  background-color: #fff; }
  .posts table {
    width: 100%;
    color: #333; }
  .posts th,
  .posts td {
    text-align: center;
    padding: 8px; }
    .posts th a,
    .posts td a {
      text-decoration: none;
      color: #000; }
      .posts th a:visited,
      .posts td a:visited {
        color: #000; }
      .posts th a:active, .posts th a:hover,
      .posts td a:active,
      .posts td a:hover {
        color: #aaa; }
  .posts tr:nth-child(even) {
    background-color: #f2f2f2; }
  .posts th {
    background-color: #4caf50;
    text-transform: capitalize;
    color: white; }

tr > td:last-child button {
  background: red;
  color: white; }
  tr > td:last-child button:hover {
    background: #d42020; }

